import React from "react";
import { NavLink, withRouter } from "react-router-dom";

import { makeStyles } from "@material-ui/core/styles";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
// import Collapse from '@material-ui/core/Collapse';
// import ExpandLess from '@material-ui/icons/ExpandLess';
// import ExpandMore from '@material-ui/icons/ExpandMore';
import PaymentIcon from "@material-ui/icons/Payment";
// import InboxIcon from '@material-ui/icons/MoveToInbox';
// import StarBorder from '@material-ui/icons/StarBorder';
import DashboardIcon from "@material-ui/icons/Dashboard";
import NotificationsIcon from "@material-ui/icons/Notifications";
import PeopleIcon from "@material-ui/icons/People";
import InsertDriveFileIcon from "@material-ui/icons/InsertDriveFile";
import BuildIcon from "@material-ui/icons/Build";
import SettingsIcon from "@material-ui/icons/Settings";

const useStyles = makeStyles((theme) => ({
  root: {
    width: "100%",
    maxWidth: 360,
    backgroundColor: theme.palette.background.paper,
  },
  link: {
    color: "inherit",
    textDecoration: "inherit",
  },
  nested: {
    paddingLeft: theme.spacing(4),
  },
}));

function _Navbar(props) {
  const classes = useStyles();
  // const [open, setOpen] = React.useState(true);

  // const handleClick = () => {
  //   setOpen(!open);
  // };
  return (
    <List
      component="nav"
      aria-labelledby="nested-list-subheader"
      // subheader={
      //     <ListSubheader component="div" id="nested-list-subheader">

      //     </ListSubheader>
      // }
      className={classes.root}
    >
      {/* <ListItem button onClick={handleClick}>
      <ListItemIcon>
        <InboxIcon />
      </ListItemIcon>
      <ListItemText primary="Inbox" />
        {open ? <ExpandLess /> : <ExpandMore />}
      </ListItem>
      <Collapse in={open} timeout="auto" unmountOnExit>
        <List component="div" disablePadding>
          <ListItem button className={classes.nested}>
            <ListItemIcon>
              <StarBorder />
            </ListItemIcon>
              <ListItemText primary="Starred" />
            </ListItem>
        </List>
      </Collapse> */}
      <NavLink className={classes.link} to="/" exact>
        <ListItem button>
          <ListItemIcon>
            <DashboardIcon />
          </ListItemIcon>
          <ListItemText primary="דף הבית" />
        </ListItem>
      </NavLink>
      <NavLink className={classes.link} to="/billing" exact>
        <ListItem button>
          <ListItemIcon>
            <PaymentIcon />
          </ListItemIcon>
          <ListItemText primary="חיובים" />
        </ListItem>
      </NavLink>
      <NavLink className={classes.link} to="/accounts" exact>
        <ListItem button>
          <ListItemIcon>
            <PeopleIcon />
          </ListItemIcon>
          <ListItemText primary="נתוני צרכנים" />
        </ListItem>
      </NavLink>
      <NavLink className={classes.link} to="/installations" exact>
        <ListItem button>
          <ListItemIcon>
            <BuildIcon />
          </ListItemIcon>
          <ListItemText primary="התקנות והחלפות" />
        </ListItem>
      </NavLink>
      <NavLink className={classes.link} to="/alerts" exact>
        <ListItem button>
          <ListItemIcon>
            <NotificationsIcon />
          </ListItemIcon>
          <ListItemText primary="קריאות והתראות" />
        </ListItem>
      </NavLink>
      <NavLink className={classes.link} to="/misc" exact>
        <ListItem button>
          <ListItemIcon>
            <InsertDriveFileIcon />
          </ListItemIcon>
          <ListItemText primary="שונות" />
        </ListItem>
      </NavLink>
      <NavLink className={classes.link} to="/settings" exact>
        <ListItem button>
          <ListItemIcon>
            <SettingsIcon />
          </ListItemIcon>
          <ListItemText primary="הגדרות" />
        </ListItem>
      </NavLink>
    </List>
  );
}

export const Navbar = withRouter(_Navbar);
